@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900|Sunflower:300,500,700);
body {
  font-family: 'Sunflower', sans-serif;
  background-color: #ececec;
  font-size: 1rem; }

.square {
  border-radius: 8px;
  box-shadow: 0 4px 14px 0 rgba(198, 202, 206, 0.3);
  background-color: #FFF; }

.btn-green {
  border-radius: 6px;
  padding: .25rem .5rem;
  display: block;
  text-align: center;
  font-weight: bold;
  line-height: 1.41;
  color: #FFF;
  text-decoration: none;
  max-width: -webkit-max-content;
  max-width: -moz-max-content;
  max-width: max-content;
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 1;
  background-color: #34c88a;
  font-size: 14px;
  border: none;
  -webkit-appearance: none; }
  @media all and (min-width: 768px) {
    .btn-green {
      padding: .75rem 1rem;
      font-size: 1rem; } }
  .btn-green:hover {
    box-shadow: 0.5em 0.5em 10px 0px rgba(226, 226, 226, 0.58);
    cursor: pointer; }

.ProgressBar {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center; }
  .ProgressBar .ProgressBar-content {
    height: 25px;
    width: 100%;
    position: relative;
    border-radius: 20px;
    border: solid 2px #f2f6f6;
    overflow: hidden;
    text-align: center;
    margin-top: 1rem; }
  .ProgressBar .ProgressBar-line {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: #34c88a; }
  .ProgressBar .ProgressBar-number {
    position: absolute;
    z-index: 2;
    top: 3px;
    font-size: 14px; }
    @media all and (min-width: 768px) {
      .ProgressBar .ProgressBar-number {
        font-size: 1rem;
        top: 2px; } }

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem; }
  @media all and (min-width: 768px) {
    .App {
      padding-top: 0;
      padding: 0;
      height: 100vh; } }
  .App .App-error {
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center; }
    .App .App-error .App-error-text {
      color: red; }
    .App .App-error .App-orange-text {
      color: #ff876c; }
  .App .App-address {
    text-align: center;
    word-break: break-all;
    margin-top: 1rem;
    font-size: 14px; }
    @media all and (min-width: 768px) {
      .App .App-address {
        font-size: 16px; } }
  .App .App-container .App-container-logos {
    display: flex;
    justify-content: space-around; }
  .App .App-container .App-container-figure {
    width: 75px;
    margin: 0 auto; }
    @media all and (min-width: 768px) {
      .App .App-container .App-container-figure {
        width: 100px; } }
    .App .App-container .App-container-figure img {
      width: 100%; }
  .App .App-container .App-content {
    width: 100%;
    margin-top: 2rem; }
    @media all and (min-width: 768px) {
      .App .App-container .App-content {
        max-width: 550px; } }
    .App .App-container .App-content .App-content-head {
      display: grid;
      grid-template-columns: 1.5fr 1.2fr 1fr;
      grid-gap: .5rem;
      justify-items: center;
      padding: 1.5rem 5px;
      background-color: #f2f6f6;
      font-size: 13px; }
      @media all and (min-width: 768px) {
        .App .App-container .App-content .App-content-head {
          font-size: 1rem;
          padding: 1.5rem; } }
    .App .App-container .App-content .App-content-details {
      display: grid;
      grid-template-columns: 1.5fr 1.2fr 1fr;
      grid-gap: .5rem;
      justify-items: center;
      padding: 1.5rem;
      background-color: #FFF;
      font-size: 1rem;
      border-bottom: solid 2px #f2f6f6; }
      @media all and (min-width: 768px) {
        .App .App-container .App-content .App-content-details {
          font-size: 18px; } }
    .App .App-container .App-content .App-content-btns {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: .5rem;
      justify-items: center;
      padding: 1.5rem 5px;
      background-color: #FFF;
      border-bottom: solid 2px #f2f6f6;
      font-size: 14px; }
      @media all and (min-width: 768px) {
        .App .App-container .App-content .App-content-btns {
          font-size: 1rem;
          padding: 1.5rem; } }

.App-stakeForm .App-stakeForm-title {
  font-size: 20px;
  color: #163f59; }

.App-stakeForm .App-stakeForm-subtitle {
  color: #868d95; }
  .App-stakeForm .App-stakeForm-subtitle.warning {
    color: red; }

.App-stakeForm .App-stakeForm-input {
  width: 100%;
  border-radius: 4px;
  border: solid 1px #e5e5e5;
  padding: .5rem 1rem;
  box-sizing: border-box;
  font-size: 17px; }
  .App-stakeForm .App-stakeForm-input:focus {
    outline: none;
    border-color: #868d95; }

.App-stakeForm .App-stakeForm-submit {
  margin-top: 1rem; }

.App-stakeForm .App-stakeForm-message {
  color: #868d95;
  margin-top: 1rem; }

.App-stakeForm .App-stakeForm-confirmation-content {
  display: flex;
  justify-content: space-between; }

